.register-page-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.register-form-container {
  padding: 3rem 2.5rem;
  width: 400px;
  background-color: var(--gray-200);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  border-radius: 0.5rem;
}

.heading {
  color: var(--red-400);
  font-weight: 700;
  font-size: 1.25rem;
  text-transform: uppercase;
}

.input-group {
  margin: 2.5rem 0 1rem 0;
  position: relative;
}

.input {
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: solid 3px #cbd5e0 !important;
  outline: none;
  color: var(--gray-500);
  font-size: 1rem;
  letter-spacing: 0.025em;
  font-weight: 600;
  transition: all 0.5s ease-out;
}

.label {
  position: absolute;
  top: 0;
  left: 0;
  color: var(--red-400);
  font-size: 0.875rem;
  letter-spacing: -0.05em;
  font-weight: 600;
  text-transform: capitalize;
  transition: all 0.5s ease-out;
}

.line {
  width: 100%;
  height: 2px;
  margin-top: 2.5rem;
  background-color: var(--gray-400);
}
.btn-design {
  width: 100%;
  background-color: var(--red-400);
  color: var(--white) !important;
  cursor: pointer;
  transition: 0.5s;
}
.btn-design:hover {
  transform: scale(1.1);
}
.cancel-register-btn {
  flex: 1 1 !important;
  background-color: var(--red-600);
  color: var(--white) !important;
}
.cancel-register-btn:hover {
  background-color: var(--red-700) !important;
}
.btn-register {
  flex: 1 1;
  margin-right: 1rem !important;
  margin-left: 0 !important;
}

.input:focus ~ .label,
.is-valid {
  top: -24px !important;
}

.input:focus,
.input:valid {
  border-bottom: solid 3px #fc8181;
}

.is-invalid {
  color: red;
}

.feedback {
  color: transparent;
}
.invalid-feedback {
  color: var(--red-400);
  font-size: 0.8rem;
}
.disable-btn {
  pointer-events: none !important;
}
.register-btns {
  display: flex;
  width: 100%;
  align-items: center;
}
.registered-message-container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.registered-message {
  height: 3.5rem;
  margin-top: 5rem;
  padding: 1rem 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffc0cb;
  font-size: 1.1rem;
  font-weight: 500;
  color: darkslategray;
  border-radius: 10px;
  box-shadow: 5px 10px 8px #888888;
}

.main {
  width: 100%;
}

.users-page-container {
  position: relative;
}
.message-notification {
  position: absolute;
  top: 5rem;
  right: 5rem;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 5px 10px 8px #888888;
  background-color: #ffc0cb;
  border: none;
  outline: none;
}
.message-notification:active {
  transform: translateY(0.2rem);
  box-shadow: 3px 5px 4px #888888;
}

.users-container {
  padding: 5rem;
