.container-section {
}
.no-users-container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.no-users {
  height: 3rem;
  margin-top: 5rem;
  padding: 1rem 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffc0cb;
  font-size: 1.1rem;
  font-weight: 500;
  color: darkslategray;
  border-radius: 10px;
  box-shadow: 5px 10px 8px #888888;
}

.register-page-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.register-form-container {
  padding: 3rem 2.5rem;
  width: 400px;
  background-color: var(--gray-200);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  border-radius: 0.5rem;
}

.heading {
  color: var(--red-400);
  font-weight: 700;
  font-size: 1.25rem;
  text-transform: uppercase;
}

.input-group {
  margin: 2.5rem 0 1rem 0;
  position: relative;
}

.input {
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: solid 3px #cbd5e0 !important;
  outline: none;
  color: var(--gray-500);
  font-size: 1rem;
  letter-spacing: 0.025em;
  font-weight: 600;
  transition: all 0.5s ease-out;
}

.label {
  position: absolute;
  top: 0;
  left: 0;
  color: var(--red-400);
  font-size: 0.875rem;
  letter-spacing: -0.05em;
  font-weight: 600;
  text-transform: capitalize;
  transition: all 0.5s ease-out;
}

.line {
  width: 100%;
  height: 2px;
  margin-top: 2.5rem;
  background-color: var(--gray-400);
}
.btn-design {
  width: 100%;
  background-color: var(--red-400);
  color: var(--white) !important;
  cursor: pointer;
  transition: 0.5s;
}
.btn-design:hover {
  transform: scale(1.1);
}
.cancel-register-btn {
  flex: 1 1 !important;
  background-color: var(--red-600);
  color: var(--white) !important;
}
.cancel-register-btn:hover {
  background-color: var(--red-700) !important;
}
.btn-register {
  flex: 1 1;
  margin-right: 1rem !important;
  margin-left: 0 !important;
}

.input:focus ~ .label,
.is-valid {
  top: -24px !important;
}

.input:focus,
.input:valid {
  border-bottom: solid 3px #fc8181;
}

.is-invalid {
  color: red;
}

.feedback {
  color: transparent;
}
.invalid-feedback {
  color: var(--red-400);
  font-size: 0.8rem;
}
.disable-btn {
  pointer-events: none !important;
}
.register-btns {
  display: flex;
  width: 100%;
  align-items: center;
}

.chat-container {
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  padding: 2rem;
}
.chat-container-inner {
}
.chat-header {
  display: table;
  margin: 0 auto;
  color: #333333;
  font-weight: 500;
  font-size: 1.2rem;
  text-transform: capitalize;
}
.chat-form {
  flex: 1 1;
  display: flex;
  align-items: start;
}
.chat-messages-container {
  flex: 1 1;
  margin: 2rem;
  margin-left: 0 !important;
  padding: 1rem;
  border: 0.4rem #87cefa solid;
  border-radius: 15px;
  min-height: 10rem;
  min-width: 15rem;
}
.chat-textarea {
  padding: 1rem;
  font-size: 1.5rem;
  font-weight: 400;
  height: 10rem;
  width: 35rem;
}
.chat-btn {
  margin: 1rem;
  flex: 1 1;
  height: 9rem;
  padding: 0 2rem;
  background-color: #616161;
  color: white;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 500;
  border-radius: 5px;
  border: none;
  outline: none;
}
.chat-btn:hover {
  background-color: #333333;
}
.chat-btn:active {
  transform: translateY(3px);
}
.name {
  text-transform: capitalize;
}
.message {
  padding-bottom: 0.5rem;
}
.from {
  color: #ff6347;
  font-size: 0.8rem;
  font-weight: 400;
  text-transform: capitalize;
}
.text {
  color: darkslategray;
  font-size: 1.1rem;
  font-weight: 400;
}
.no-messages-container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.no-messages {
  width: 23rem;
  height: 3rem;
  margin-top: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffc0cb;
  font-size: 1.1rem;
  font-weight: 500;
  color: darkslategray;
  border-radius: 10px;
  box-shadow: 5px 10px 8px #888888;
}
/*   MEDIA QUERIES */

